.backdrop {
  display: flex;
  top: 0;
  left: 0;
  background-color: rgba(66, 66, 66, 0.2);
  width: 100vw;
  height: 100vh;
  position: fixed;
  opacity: 0;
  pointer-events: none;

  transition: opacity 300ms linear;
}
.backdrop.is-open {
  pointer-events: visible;
  opacity: 1;
}
